import { mapGetters } from 'vuex'
import defaultAvatar from '@/assets/images/avatars/avatar1@2x.png'
import Chip from '@/components/ChipComponent'
import SubscribeForm from '@/components/SubscribeFormComponent'

import { butter } from '@/buttercms'

export default {
  name: 'BlogPost',
  components: { Chip, SubscribeForm },
  layout: 'blog',
  data () {
    return {
      // title: this.oneBlog.title,
      // description: this.oneBlog.meta_description,
      // image: this.oneBlog.featured_image,
      defaultAvatar,
      oneBlog: {
        author: {
          first_name: '',
          last_name: '',
          profile_image: ''
        }
      },
      breadcrumb: [
        {
          text: 'Main',
          href: '/blog'
        },
        {
          text: 'Branding & Marketing',
          href: '#'
        }
      ],
      chipName: 'Marketing'
    }
  },
  created () {
    this.$store.dispatch('blog/fetchBlogList')
    this.getPost()
  },
  head () {
    return {
      title: this.oneBlog.title,
      meta: [
        { charset: 'utf-8' },
        {
          name: 'viewport',
          content: 'width=device-width, initial-scale=1'
        },
        {
          hid: 'title',
          name: 'title',
          content: this.oneBlog.title
        },
        {
          hid: 'description',
          name: 'description',
          content: this.oneBlog.meta_description
        },
        {
          hid: 'twitter:title',
          name: 'twitter:title',
          content: this.oneBlog.title
        },
        {
          hid: 'twitter:description',
          name: 'twitter:description',
          content: this.oneBlog.meta_description
        },
        {
          hid: 'twitter:image',
          name: 'twitter:image',
          content: this.oneBlog.featured_image
        },
        {
          hid: 'twitter:image:alt',
          name: 'twitter:image:alt',
          content: this.oneBlog.title
        },
        {
          hid: 'og:title',
          property: 'og:title',
          content: this.oneBlog.title
        },
        {
          hid: 'og:description',
          property: 'og:description',
          content: this.oneBlog.meta_description
        },
        {
          hid: 'og:site_name',
          name: 'og:site_name',
          content: 'GINI'
        },
        {
          hid: 'og:type',
          name: 'og:type',
          content: 'article'
        },
        {
          hid: 'og:url',
          name: 'og:url',
          content: 'https://www.gininow.com/blog/' + this.oneBlog.slug
        },

        {
          hid: 'og:image',
          property: 'og:image',
          content: this.oneBlog.featured_image
        },
        {
          hid: 'og:image:secure_url',
          property: 'og:image:secure_url',
          content: this.oneBlog.featured_image
        },
        {
          hid: 'og:image:alt',
          property: 'og:image:alt',
          content: this.oneBlog.title
        }
      ]
    }
  },
  computed: {
    ...mapGetters({
      blogList: 'blog/getBlogList'
    }),
    enableOneBlogRendering () {
      console.log(this.oneBlog, Object.values(this.oneBlog), 'hey')
      return 1
    }
  },
  methods: {
    formatCompat (date) {
      const ms = [
        'Jan',
        'Feb',
        'Mar',
        'Apr',
        'May',
        'Jun',
        'Jul',
        'Aug',
        'Sep',
        'Oct',
        'Nov',
        'Dec'
      ]
      return (
        ms[date.getMonth()] + ' ' + date.getDate() + ', ' + date.getFullYear()
      )
    },
    getPost () {
      butter.post
        .retrieve(this.$route.params.slug)
        .then((res) => {
          if (res && res.statusText === 'OK') {
            this.oneBlog = res.data.data
          }
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err)
        })
    }
  }
}
